.carouselItemContainer{
   height: 300px;
   
   margin: 100px;
   outline: none;


   



 
}
.carouselItem{
   position: relative;
   background-color: chartreuse;

   
}
.click{
   width: 300px;
   height: 200px;
   background-color: blue;
   transform: perspective(600px) rotateY(-30deg);
   transition: transform .3s;
   position: absolute;
   margin-left: 65px;
   margin-top: 50px;
 

}
.projectPhoto{
   width: 300px;
   height: 200px;
   margin-left: 65px;
   margin-top: 50px;
   transform: perspective(600px) rotateY(-30deg);
   transition: transform .3s;
   position: absolute;  
  
}

.projectPhoto:hover{
   
   cursor: pointer;
   transform: rotateY(0);

}




.projectText{
   color: #FF0099;
   font-family: 'Inika', serif;
   letter-spacing: .2rem;
   transition: transform .3s;
   
   
  
}