@import url('https://fonts.googleapis.com/css2?family=Inika&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DynaPuff&display=swap');

.App {
  text-align: center;
  


}

.canvas {

  height: 500px;


}

.headerMarquee {
  color: #FF0099;
  font-family: 'DynaPuff', cursive;
  letter-spacing: .2rem;

  width: 1500px
}
::selection{
background: #FF7CAB;
}

.canvasContainer {

  height: 500px;



}

.headerWelcome {
  color: #FF0099;

}

.spanHeader {
  color: #207924;
}

.spanProject {
  color: white;
}

.spaContact {
  color: #FF0099;
}


.photo {
  height: 200px;
  width: 200px;
  background-color: #FF0099;
  border-radius: 100px;
  background-image: url('./img/IMG_2150.jpg');
  background-size: cover;
  position: absolute;
  top: 40px;
  left: 50px;

  z-index: 4;

}

.contentContainer {
  display: flex;
  justify-content: center;


}

.content {
  width: 1519px;
  height: 1000px;
  position: relative;

  margin-top: 20px;

  padding: 0;






}

.contactContainer {

  width: 1519px;
  height: 400px;

}


.textAbout {
  width: 500px;
  height: 300px;
  background-color: #FF7CAB;
  border-radius: 50px;
}

.aboutContainer {
  height: 200px;
  width: 1500px;

  position: absolute;
  top: 0;
  z-index: 1;

}

.projectContainer {
  background-color: #FBE8EE;
  height: 800px;
  position: absolute;
  top: 200px;
  z-index: 2;
  width: 1519px;
  display: flex;
  left: 0;


}

.About {
  width: 1500px;
  height: 300px;

  position: absolute;
  top: 60px;

  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;



}

.photoContainer {


  position: relative;
  width: 300px;
  height: 300px;


}

.myPhoto {
  width: 250px;
  height: 250px;
  position: absolute;


  z-index: 4;
  top: 15px;

  left: 25px;
  animation: myphoto 40s infinite ease-in;


}

.text {
  color: #FBE8EE;

  font-family: 'DynaPuff', cursive;
  height: 210px;

}

.resume {
  background-color: white;
  border: none;
  width: 350px;
  height: 35px;
  border-radius: 8px;
  color: #FF00A8;
  font-family: 'DynaPuff', cursive;
}

.mambl1 {

  animation: mambl1 40s infinite ease-in;
  transform: translateX(100px) translateY(500px);


}

.mambl2 {

  animation: mambl2 40s infinite ease-in;
  transform: translateX(1100px) translateY(500px) rotate(-90deg);
  opacity: 0.5;


}

.mambl3 {

  animation: mambl3 30s infinite ease-in;
  transform: rotate(-270deg);
  opacity: 0.5;


}

.project {
  position: absolute;
  width: 1500px;
  height: 500px;

  top: 450px;
  z-index: 5;
}

.Marquee {
  color: #ffffff;
  font-family: 'DynaPuff', cursive;
}

.projectItems {

  width: 1519px;
  height: 500px;

  display: flex;
  justify-content: center;
}

.projectItem {

  width: 1300px;
  height: 500px;

}


.marqueeContact {
  font-family: 'DynaPuff', cursive;
  color: #FF0099;
}

.contact {

  display: flex;
  justify-content: center;
  gap: 100px;
  margin-top: 80px;
}

.contactItem {
  width: 40px;
  height: 40px;
  background-color: #e75c8a;
  padding: 20px;
  border-radius: 70px;
  transition: 0.3s ease-in-out 0s;



}

.contactItem * {
  fill: white;


}


.contactItem:hover {



  cursor: pointer;
  background-color: white;
}



.i {
  width: 40px;
  height: 40px;


}

.contactItem:hover * {
  fill: #e75c8a;

}




.languageContainer{
  width:100%;
  height: 40px;

  position: relative;
  top: 20px;
  display: flex;
  justify-content: center;


  

  
}
.language{
  width: 1500px;

  display: flex;
  justify-content: end;
  gap: 30px;
  padding-right: 40px;
  
}
.languageSvg *{
  fill: #FF0099;

}
.menuSvg{
  width: 40px;
  height: 40px;
  cursor: pointer;

}


.menuSvg *{
  fill: #FF0099;
}
.languageSvg{
  cursor: pointer;
}
@keyframes myphoto {
  0% {
    transform: rotate(0deg);

  }

  100% {
    transform: rotate(180deg);

  }
}

@keyframes mambl3 {
  0% {
    transform: translateX(100px) translateY(0px) rotate(-270deg);
  }

  50% {
    transform: translateX(200px) translateY(400px) rotate(-270deg);
  }

  51% {
    transform: translateX(200px) translateY(400px) rotate(270deg);
  }


  100% {
    transform: translateX(100px) translateY(0px) rotate(270deg);


  }
}

@keyframes mambl1 {
  0% {

    transform: red translateX(0) translateY(0px);
  }

  60% {

    transform: translateX(500px) translateY(400px);

  }

  62% {
    transform: translateX(500px) translateY(400px) rotate(-90deg)
  }

  80% {

    transform: translateX(0px) translateY(0px) rotate(-90deg);
  }

  82% {

    transform: translateX(0px) translateY(0px) rotate(90deg);
  }

  100% {

    transform: translateX(100px) translateY(500px) rotate(90deg);
  }
}

@keyframes mambl2 {
  0% {

    transform: red translateX(0) translateY(0px);
  }

  50% {

    transform: translateX(800px) translateY(400px);

  }

  52% {
    transform: translateX(800px) translateY(400px) rotate(0deg)
  }

  70% {

    transform: translateX(1000px) translateY(0px) rotate(-90deg);
  }

  72% {

    transform: translateX(1000px) translateY(0px) rotate(90deg);
  }

  100% {

    transform: translateX(1100px) translateY(500px) rotate(90deg);
  }
}